body {
  background: rgb(157, 158, 166);
  background: linear-gradient(
    90deg,
    rgba(157, 158, 166, 1) 0%,
    rgba(255, 252, 248, 1) 31%,
    rgba(217, 217, 217, 1) 99%
  );
}
.header {
  display: flex;
  justify-content: center;

  width: 100%;
  height: 100px;
  background-color: white;
}
.logo {
  height: 100px;
}
.header__container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1440px;
}
.header_nav_item {
  margin-top: 32px;
}
.portfoli__container {
  gap: 50px;
  display: flex;
  justify-content: center;
  margin: 0 auto;

  max-width: 1440px;
  margin-top: 150px;
}
.myPhoto {
  width: 500px;
  height: 600px;
}
.portfoli__about {
  background-color: #fff;
  width: 700px;
  height: 600px;
}
ul {
  display: flex;
  gap: 30px;
  font-size: 27px;
}
li {
  margin-top: 8px;
}
h1 {
  text-align: center;
  font-size: 44px;
  margin-bottom: 100px;
}

a {
  text-decoration: none;
  color: black;
}
.portfoli__list {
  justify-content: center;
  margin-left: 40px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 50px;
}
h2 {
  text-align: center;
  font-size: 44px;
}
.TodoList {
  width: 1440px;
  margin: 0 auto;
  justify-content: space-between;
  gap: 40px;
  display: flex;
}

.Cards {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: start;
  margin: 0 auto;
  width: 1440px;
}
.Card {
  width: 300px;
  height: 200px;
  border: 3px black solid;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: 600;
}
li {
  list-style-type: none;
}
@media (max-width: 600px) {
}
.Temi {
  width: 1440px;
  margin: 0 auto;
  justify-content: center;
  gap: 40px;
  display: flex;
  align-items: center;
}
.NumberOfTema {
  padding-top: 60px;
  width: 800px;
  height: 100px;
  text-align: center;
  background-color: whitesmoke;
  margin: 0 auto;
  margin-bottom: 100px;
  border: 3px black solid;
  justify-content: center;
}
